<template>
		<v-data-table
			title="Select a race to engage with"
			:headers="headers"
			:items="users2render"
			class="elevation-1"
			:loading="users2render === undefined"
			fixed-header
			dense
		>
			<template slot="top">
				<v-toolbar-title style="padding: 20px; text-align: center">
					Sellers race ranking
				</v-toolbar-title>
			</template>
			<template slot="item.name" slot-scope="{ item }">
				<div :class="item.me ? 'me' : ''">
					{{ item.name }}
				</div>
			</template>
		</v-data-table>
</template>

<script>
const WinScore = 20;
export default {
	props: {
		user: Object,
		myId: String,
	},
	data() {
		return {
			snapshootUnsubscribe: null,
			headers: [
				{ text: "Seller name", value: "name" },
				{ text: "proposals", value: "proposals" },
				{ text: "score", value: "score" },
				{ text: "toWin", value: "toWin" },
			],
		};
	},
	computed: {
		users2render() {
			return Object.entries(this.user.sellers)
				.map((s) => {
					return {
						...s[1],
						name: s[0],
						toWin: WinScore - s[1].score,
						me: this.myId && s[0] === this.myId,
					};
				})
				.sort((a, b) => b.score - a.score);
		},
	},
};
</script>

<style>
.me {
	font-size: large;
	font-weight: bolder;
	color: green;
}
</style>
